.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.post-header {
  text-align: center;
  font-family: 'Arvo', serif;
  font-size: 18px;
  line-height: 24px;
  font-weight: bold;
  color: #000;
  padding: 3%;
}

.post-extra {
  text-align: center;
  font-family: Helvetica, Sans-Serif;
  color: #999;
  font-size: 12px;
}

.navbar-tab {
  color: #57a2e4 !important;
}

.icon-clicked {
  color: red;
}
